* {
  margin: 0;
  padding: 0;
}
#github-search {
  width: 100%;
  border-radius: 10px;
  display: flex;
  gap: 20px;
  background-color: #c7b7c7;
  justify-content: center;
  padding-bottom: 10vh;
  margin-bottom: 5vh;
  flex-direction: column;
  align-items: center;
}
#github-search .github-header {
  font-size: 4rem;
  text-shadow: -3px 4px 4px #508d88;
  font-weight: 700;
  letter-spacing: 5px;
  padding: 10vh;
}
#github-search .github-search-input {
  width: 20vw;
  outline: none;
  font-size: 25px;
  padding: 10px;
  border: 0;
  border-radius: 10px;
  height: 18px;
  box-shadow: -4px 1px 4px #000000;
  border-style: none;
}
#github-search button {
  border-radius: 20%;
  border: 0;
  box-shadow: 2px 2px 4px #000000;
  zoom: 200%;
  margin: 0 auto;
  width: 50px;
}
#github-search button:hover {
  border-radius: 20%;
  border: 0;
  box-shadow: 2px 2px 4px #000000;
  zoom: 210%;
  margin: 0 auto;
  width: 50px;
}
