* {
  margin: 0;
  padding: 0;
}
body {
  background-color: antiquewhite;
}
.github-list {
  box-sizing: border-box;
  width: 1042px;
  border: 1px solid #000000;
  height: max-content;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.github-list .github-item {
  position: relative;
  box-sizing: border-box;
  height: 240px;
  width: 200px;
}
.github-list .github-item img {
  width: 200px;
  height: 200px;
  border: 1px solid #000000;
}
.github-list .github-item .github-userName {
  text-align: center;
  font-size: 1.4rem;
  bottom: 0;
  position: absolute;
}
